import React, { Component, Fragment } from "react";
import { connect } from "react-redux";

import lazyLoadImages from "../../utilities/imagesLazyLoad";
import {isMobile} from '../../utilities/utilities';
import * as actionCreators from "../../store/actions/index";
import Button from "../../components/UI/Button/Button";
import Modal from "../../components/UI/Modal/Modal";
import Spinner from "../../components/UI/Spinner/Spinner";
import ProgressBar from "../ProgressBar/ProgressBar";
import CardControls from "../../components/CardControls/CardControls";

import { Lightrope, Present } from "../../HOC/Themes/Christmas/Christmas";

class WordsConstructor extends Component {
  constructor(props) {
    super(props);
    this.inputFocused = React.createRef();
    this.imgBlurred = React.createRef();
    this.state = {
      currentWord: 0,
      correctWords: 0,
      increment: 0,
      showPresentWhenPercentCorrect: 70, // if correct words are 70% out of total show presents
      showPresent: false,
      try: 3,
      value: "",
      data: null,
      modalIsOn: false,
      inputConfig: {
        maxLength: 1,
        className: "blank-letter",
        readOnly: false
      }
    };
  }

  componentDidMount() {
    this.props.fetchData(this.props.url);
    if(isMobile()) {
      let updatedInputConfig = {...this.state.inputConfig, readOnly: true};
      this.setState({inputConfig: updatedInputConfig});
    }
    if (this.inputFocused.current) this.inputFocused.current.focus();
  }

  componentDidUpdate() {
    window.scrollTo(0, 0);
    lazyLoadImages();
    if (this.props.data && !this.state.showPresent) this.checkPercentage();
    if (this.inputFocused.current) this.inputFocused.current.focus();
  }

  checkPercentage = () => {
    if (
      this.state.correctWords / this.props.data.length >=
        this.state.showPresentWhenPercentCorrect / 100 &&
      this.state.currentWord === this.props.data.length
    ) {
      this.setState({ showPresent: true });
    }
  };

  reloadApp = () => {
    this.setState({
      currentWord: 0,
      correctWords: 0,
      increment: 0,
      showPresentWhenPercentCorrect: 80,
      showPresent: false,
      try: 3,
      value: "",
      data: null,
      modalIsOn: false
    });
  };

  checkAnswer = e => {
    let target = null;
    let currentWord = this.props.data[this.state.currentWord].word;
    let letter = currentWord[this.state.increment];
    if (e.type === "click") {
      target = this.inputFocused.current;
    } else if (e.type === "change") {
      target = e.target;
      this.checkKeyboardLayout(e.target.value.toLowerCase());
    }

    if ((target.value || e.target.dataset.value) !== letter) {
      target.classList.add("-wrong");
      this.setState(prevState => ({
        try: prevState.try - 1,
        value: prevState.value
      }));
    } else {
      this.hideGuessedLetters(letter);
      target.classList.remove("-wrong");
      target.classList.add("-correct");

      this.setState(prevState => ({
        increment: prevState.increment + 1,
        value: prevState.value + letter
      }));

      if (this.state.increment === currentWord.length - 1) {
        this.imgBlurred.current.style.filter = "none";
        this.voiceOver();
        // setTimeout(() => {
        //   if (this.state.try > 1) {
        //     this.clearLetters("-correct");
        //     this.setState(prevState => ({
        //       correctWords: prevState.correctWords + 1,
        //       increment: 0,
        //       try: 3,
        //       currentWord: prevState.currentWord + 1,
        //       value: ""
        //     }));
        //   } else {
        //     this.clearLetters("-wrong");
        //     this.setState(prevState => ({
        //       increment: 0,
        //       try: 2,
        //       currentWord: prevState.currentWord + 1,
        //       value: ""
        //     }));
        //   }
        //   if (this.imgBlurred.current)
        //     this.imgBlurred.current.style.filter = "";
        // }, 2500);
      }
    }
  };

  hideGuessedLetters = letter => {
    let letters = document.querySelectorAll(".letter:not(.-hidden)");
    for (let i = 0; i < letters.length; i++) {
      if (letters[i].dataset.value === letter) {
        letters[i].classList.add("-hidden");
        break;
      }
    }
  };

  voiceOver = () => {
    if (!("speechSynthesis" in window)) return;
    let speech = new SpeechSynthesisUtterance();
    let speak = window.speechSynthesis;
    speech.lang = "en";
    speech.rate = 0.8;
    speech.text = this.props.data[this.state.currentWord].word;
    speak.speak(speech);
  };

  checkKeyboardLayout = letter => {
    const engChars = "abcdefghijklmnopqrstuvwxyz -";
    if (!engChars.includes(letter)) {
      this.setState(prevState => {
        return { modalIsOn: !prevState.modalIsOn };
      });
    }
  };

  handleControlButtons = e => {
    if (e.target.classList.contains("next-word")) {
        if (this.state.try > 1) {
          this.clearLetters("-correct");
          this.setState(prevState => ({
            correctWords: prevState.correctWords + 1,
            increment: 0,
            try: 3,
            currentWord: prevState.currentWord + 1,
            value: ""
          }));
        } else {
          this.clearLetters("-wrong");
          this.setState(prevState => ({
            increment: 0,
            try: 2,
            currentWord: prevState.currentWord + 1,
            value: ""
          }));
        }
     if (this.imgBlurred.current)
          this.imgBlurred.current.style.filter = "";   
    } else if (e.target.classList.contains("skip-word")) {
      this.clearLetters("-wrong");
      this.voiceOver();
      this.setState({
        value: this.props.data[this.state.currentWord].word,
        increment: 200
      });
    }
  };

  clearLetters = type => {
    let blankLetters = document.querySelectorAll(".blank-letter");
    let hiddenLetters = document.querySelectorAll(".-hidden");
    let roster = document.querySelector(".roster");
    if (roster) {
      roster.children[this.state.currentWord].classList.add(type);
    }
    blankLetters.forEach(elem => elem.classList.remove("-correct", "-wrong"));
    hiddenLetters.forEach(elem => elem.classList.remove("-hidden"));
    blankLetters.forEach(elem => {
      elem.value = "";
    });
  };

  closeModal = () => {
    this.setState(prevState => {
      return { modalIsOn: !prevState.modalIsOn };
    });
  };

  render() {
    let wordCard,
      roster,
      data = null;
    if (this.props.loading) {
      wordCard = <Spinner />;
    } else if (this.props.error) {
      wordCard = <div>Oops, something went wrong...</div>;
    } else if (this.props.data && this.props.data[this.state.currentWord]) {
      data = this.props.data[this.state.currentWord];
      wordCard = (
        <Fragment>
          <div className="card-content">
            <h3 className="word-explanation">{data.translation}</h3>
            <form>
              <div className="puzzle-set">
                <ol className="blank-letters">
                  {data.word.split("").map((elem, index) => (
                    <input
                      {...this.state.inputConfig}
                      ref={
                        index === this.state.increment
                          ? this.inputFocused
                          : null
                      }
                      key={index}
                      disabled={index !== this.state.increment}
                      value={this.state.value[index] || ""}
                      data-value={elem}
                      onChange={this.checkAnswer}
                    />
                  ))}
                </ol>
                <ul className="letters-set">
                  {this.props.data[this.state.currentWord].shakenArray.map(
                    (elem, index) => {
                      return (
                        <li
                          className="letter"
                          data-value={elem}
                          key={index}
                          onClick={this.checkAnswer}
                        >
                          {elem}
                        </li>
                      );
                    }
                  )}
                </ul>
              </div>
            </form>
          </div>
          <CardControls
            voiceOver={this.voiceOver}
            click={this.handleControlButtons}
            data={data}
            ref={this.imgBlurred}
          />
        </Fragment>
      );
      roster = this.props.data.map(elem => (
        <li className="list-item" key={elem.word}>
          <span className="translation">{elem.translation}</span>
          <span className="word">{elem.word}</span>
        </li>
      ));
    }

    return (
      <Fragment>
        <div className="wrapper">
          <Lightrope />
          {this.state.showPresent ? <Present /> : null}
          {this.props.data ? (
            <ProgressBar
              props={{
                correctWords: this.state.correctWords,
                totalWords: this.props.data.length,
                doneWords: this.state.currentWord
              }}
            />
          ) : null}
          <div className="card-container">{wordCard}</div>

          <ol className="roster">{roster}</ol>
          {this.state.modalIsOn ? (
            <Modal show={this.state.modalIsOn}>
              <span>Switch your keyboard to English!</span>
              <Button classes="Success" click={this.closeModal}>
                Ok
              </Button>
            </Modal>
          ) : null}
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.wordsConstructor.fetchedData,
    loading: state.wordsConstructor.loading,
    error: state.wordsConstructor.error,
    url: state.wordsConstructor.url
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchData: url => dispatch(actionCreators.asyncFetchStart(url))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WordsConstructor);



